import React, { Component } from 'react';
import { Button, Dropdown, Form, Grid, GridColumn, Icon, Label } from 'semantic-ui-react';
import Panel from '../../../../../commons/components/panel/Panel';
import MultiInputField from '../../../../../commons/components/form/MultiInputField';
import InputField from '../../../../../commons/components/form/InputField';
import { FieldState, FormState } from 'formstate';

interface IsearchReviewStationLogsProps {
  handleOnSubmit(list: any[]): void;
}

class SearchReviewStationLogs extends Component<IsearchReviewStationLogsProps> {
  elements: any;
  form!: FormState<any>;

  constructor(props: IsearchReviewStationLogsProps) {
    super(props);
    this.elements = {};
    this.initForm();
  }

  initForm = () => {
    const form = new FormState({
      stationType: new FormState({
        ft: new FieldState(''),
        in: new FieldState('')
      }),
      from: new FieldState(''),
      to: new FieldState(''),
      bol: new FieldState(''),
      pickup: new FieldState(''),
      ticket: new FieldState(''),
      conf: new FieldState('')
    });
    this.form = form;
  };
  onChangeField = (event: any, data: any) => {
    const elements = this.elements;
    elements[data.name] = data.value || data.checked;
  };
  onSubmit = (e: any, data: any) => {
    this.props.handleOnSubmit(this.elements);
  };

  render() {
    const form = this.form;
    return (
      <Panel header={'FILTER OPTIONS'}>
        <Form size="mini" onSubmit={this.onSubmit} style={{ width: '100%' }}>
          <Grid doubling container padded centered columns="equal">
            <GridColumn textAlign="right" width={8}>
              <Grid doubling container padded centered columns="equal">
                <Grid.Row>
                  <Grid.Column textAlign="right">
                    <Label>Date Range:</Label>
                  </Grid.Column>
                  <Grid.Column textAlign="left">
                    <Form.Field inline>
                      <InputField
                        label="From"
                        labelStyle={{ minWidth: '22px' }}
                        inline
                        labelPosition="left"
                        type="date"
                        fieldState={form.$.from}
                      />
                    </Form.Field>
                    <InputField
                      label="To"
                      inline
                      labelStyle={{ minWidth: '22px' }}
                      labelPosition="left"
                      type="date"
                      fieldState={form.$.to}
                    />
                  </Grid.Column>
                </Grid.Row>
              </Grid>

              <MultiInputField
                inline
                labels={['PickUp', 'Drop Off']}
                label="Station Type:"
                labelPosition="left"
                labelStyle={{ minWidth: '85px', margin: 'inherit' }}
                type="checkbox"
                fieldState={form.$.stationType}
              />

              <Form.Field inline>
                <Label>PickUp/Drop Off...</Label>
                <Dropdown
                  placeholder="PickUp/Drop Off..."
                  search
                  selection
                  options={[
                    { key: 11, value: '1', text: 'To Ask' },
                    { key: 21, value: '2', text: 'LAKE CHARLES SOUTH (WESTLAKE)' }
                  ]}
                />
              </Form.Field>
              <Form.Field inline>
                <Label>PickUp/Drop Off...</Label>
                <Dropdown
                  placeholder="PickUp/Drop Off..."
                  search
                  selection
                  options={[
                    { key: 11, value: '1', text: 'To Ask' },
                    { key: 21, value: '2', text: 'LAKE CHARLES SOUTH (WESTLAKE)' }
                  ]}
                />
              </Form.Field>
            </GridColumn>
            <GridColumn textAlign="right" width={8}>
              <Form.Field inline>
                <Label>Driver:</Label>
                <Dropdown
                  search
                  name={'driver'}
                  onChange={this.onChangeField}
                  selection
                  options={[{ key: 1, value: '1', text: 'NAYLOR, KELVIN' }]}
                />
              </Form.Field>
              <Form.Field inline>
                <Label>Company Name:</Label>
                <Dropdown
                  search
                  selection
                  name={'companyname'}
                  onChange={this.onChangeField}
                  options={[{ key: 1, value: '1', text: 'Company 1' }]}
                />
              </Form.Field>
              <InputField
                label="BOL #"
                inline
                labelStyle={{ minWidth: '90px' }}
                fluid
                labelPosition="left"
                type="text"
                fieldState={form.$.bol}
              />
              <InputField
                label="Ticket #"
                inline
                labelStyle={{ minWidth: '90px' }}
                fluid
                labelPosition="left"
                type="text"
                fieldState={form.$.ticket}
              />
              <InputField
                label="Confirmation #"
                inline
                labelStyle={{ minWidth: '90px' }}
                fluid
                labelPosition="left"
                type="text"
                fieldState={form.$.conf}
              />
            </GridColumn>
          </Grid>
          <Button icon labelPosition="right">
            Search
            <Icon name="search" />
          </Button>
          <Button icon labelPosition="right">
            Clear Filter
            <Icon name="refresh" />
          </Button>
        </Form>
      </Panel>
    );
  }
}

export default SearchReviewStationLogs;
